  align-items: center;
  justify-content: center;
}
.calculate-table-wrapper::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: 100%;
  aspect-ratio: 1;
  background-color: var(--color-dark);
  border-radius: 50%;
}
@media (max-width: 768px) {
  .calculate-table-wrapper::before {
    display: none;
  }
}
.calculate-table {
  position: relative;
  border-collapse: collapse;
}
.calculate-table th {
  color: var(--color-light);
  padding-block: 20px;
  text-align: left;
  text-transform: uppercase;
}
.calculate-table td {
  padding-block: 15px;
  color: var(--color-light-gray);
}
.calculate-table tbody td {
  border-top: 1px solid var(--color-light-gray);
}
.calculate-table tbody tr:last-child td:first-child {
  position: relative;
}
.calculate-table tbody tr:last-child td:first-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 70%;
  height: 1px;
  background-color: var(--color-light-gray);
}
.calculate-table tfoot {
  color: var(--color-light);
}
.calculate-table tfoot td {
  padding-top: 33px;
  padding-bottom: 0;
  color: currentColor;
}
@media (max-width: 1024px) {
  .calculate-table tfoot td {
    padding-top: 15px;
  }
}
.calculate-table :is(td, th) {
  padding-right: 30px;
}
@media (max-width: 1024px) {
  .calculate-table :is(td, th) {
    padding-block: 10px;
  }
}
.calculate-table :is(td, th):not(:first-child) {
  padding-left: 30px;
}
.calculate-table :is(td, th):not(:last-child) {
  border-right: 1px solid var(--color-light-gray);
}

.footer {
  background-color: var(--color-dark);
  --footer-logo-width: 20.77%;
}
.footer-main {
  padding-block: 150px;
}
@media (max-width: 1024px) {
  .footer-main {
    padding-block: 80px;
  }
}
@media (max-width: 768px) {
  .footer-main {
    padding-block: 50px;
  }
}
.footer-main__info, .footer-main__content {
  display: grid;
  column-gap: 62px;
}
.footer-main__info {
  grid-template-columns: var(--footer-logo-width) auto;
}
.footer-main__info:not(:last-child) {
  margin-bottom: 60px;
}
@media (max-width: 768px) {
  .footer-main__info:not(:last-child) {
    margin-bottom: 30px;
  }
}
@media (max-width: 1024px) {
  .footer-main__info {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }
}
.footer-main__text {
  text-align: left;
}
.footer-main__text a {
  color: currentColor;
  text-decoration: underline;
}
.footer-main__content {
  grid-template-columns: var(--footer-logo-width) repeat(3, 1fr);
}
@media (max-width: 1024px) {
  .footer-main__content {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 30px;
  }
}
@media (max-width: 768px) {
  .footer-main__content {
    grid-template-columns: 1fr;
  }
}
.footer-main__column-title {
