  font-size: clamp(16px, 1.15vw, 22px);
  font-weight: 700;
  line-height: 1.72;
  text-transform: uppercase;
}
.footer-main__column-title:not(:last-child) {
  margin-bottom: 15px;
}
.footer-main__column-title.has-arrow::after {
  content: "";
  display: inline-block;
  height: 0.74em;
  aspect-ratio: 1;
  background: url(../img/bold-arrow-down.svg) center/contain no-repeat;
  scale: -1 1;
  margin-left: 0.5em;
}
.footer-main__column-text {
  max-width: 235px;
  color: var(--color-light);
  font-size: 16px;
  font-weight: 300;
  line-height: 1.75;
  letter-spacing: 0.8px;
}
.footer-main__column-schedule {
  color: var(--color-light);
}
.footer-main__column-schedule:not(:last-child) {
  margin-bottom: 13px;
}
.footer-main__column-address {
  color: var(--color-light);
  font-style: normal;
}
.footer-main__column-address:not(:last-child) {
  margin-bottom: 35px;
}
.footer-main__column-item {
  color: var(--color-light);
}
.footer-main__column-item:not(:last-child) {
